<template>
  <div
    class="com-map_contianer"
    v-loading="loading"
    element-loading-text="拼命加载中"
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.5)">
    <header>
      <div class="left">
        <h1>{{title || '客户数据地图'}}</h1>
      </div>
    </header>
    <section style="flex: none">
      <div class="map" ref="map-box"></div>
    </section>
  </div>
</template>
<script>
import echarts from 'echarts'
import 'echarts/extension/bmap/bmap'
import styleJson from '@/common/map-style'
const BMap = window.BMap
const symbol = 'image://data:image/png;base64,'
/**
 * @description 创建圆形覆盖物
 * @param {Number} lng 经度
 * @param {Number} lat 纬度
 * @param {Number} radius 半径（以 km 为单位）
 * @param {Number} opacity 透明度
 * @param {String} color 颜色
 * @return {Object} 圆形覆盖物
 */
function createCircleOverlay (lng, lat, radius = 30, opacity = 0.6, color = '#219dd0') {
  const point = new BMap.Point(lng, lat)
  return new BMap.Circle(point, radius * 1000, {
    strokeColor: color,
    strokeWeight: 2,
    strokeOpacity: opacity,
    fillColor: color,
    fillOpacity: opacity
  })
}
export default {
  props: {
    center: {
      type: Array,
      default: () => [120.918964, 31.385231]
    },
    zoom: {
      type: Number,
      default: 11
    },
    title: {
      type: String,
      required: true
    },
    // 二级厂数据
    // [经度, 纬度, 简称, 全称, 地址, 电话, 法人]
    twoLevelData: {
      type: Array
    },
    // 三级厂数据
    threeLevelData: {
      type: Array
    },
    twoLevelTooltipFormatter: {
      type: Function,
      default: function ({ data }) {
        const name = data[3]
        const address = data[4]
        const phone = data[5]
        return `名称: ${name} <br> 地址: ${address} <br> 电话: ${phone}`
      }
    },
    threeLevelTooltipFormatter: {
      type: Function,
      default: function ({ data }) {
        let address = data[3] || ''
        address = address.replace(/\^/g, '')
        return `企业名称: ${data[2]}<br>地址: ${address}<br>法人: ${data[4]}`
      }
    },
    // 外圈半径
    logisticsRangeInnerRadius: {
      type: Number,
      default: 30
    },
    // 内圈半径
    logisticsRangeOuterRadius: {
      type: Number,
      default: 60
    }
  },
  data () {
    return {
      list: [],
      loading: false
    }
  },
  mounted () {
    this.loading = true
    this.initBmapChart()
  },
  methods: {
    // 数据赋值完成后必须调用
    dataCompleted () {
      this.$nextTick(() => {
        this.mapChart.setOption({
          series: [{
            id: 'threeLevelPlant',
            name: '三级厂',
            data: this.threeLevelData
          }]
        })
        this.loading = false
      })
    },
    /**
     * 初始化地图
     */
    initBmapChart () {
      const that = this
      const mapChart = that.mapChart = echarts.init(that.$refs['map-box'])
      const option = {
        animation: false,
        tooltip: {
          trigger: 'item'
        },
        bmap: {
          center: that.center,
          zoom: that.zoom,
          roam: true,
          mapStyle: {
            styleJson
          }
        },
        series: [{
          id: 'twoLevelPlant',
          name: '二级厂',
          type: 'scatter',
          coordinateSystem: 'bmap',
          symbol: 'circle',
          symbolSize: 60,
          itemStyle: {
            color: 'rgba(39, 146, 255, 0.9)'
          },
          label: {
            show: true,
            fontSize: 12,
            color: '#fff',
            formatter (params) {
              return params.data[2] // 简称
            }
          },
          tooltip: {
            formatter: that.twoLevelTooltipFormatter
          },
          data: that.twoLevelData,
          zlevel: 5,
          animation: false
        },
        {
          id: 'threeLevelPlant',
          name: '三级厂',
          type: 'scatter',
          coordinateSystem: 'bmap',
          symbol,
          symbolSize: 14,
          label: {
            show: false
          },
          tooltip: {
            formatter: that.threeLevelTooltipFormatter
          },
          data: that.threeLevelData,
          animation: false
        }
        ]
      }
      mapChart.setOption(option)
      that.$nextTick(() => {
        that.mapSetting()
        that.initEvent()
      })
    },
    mapSetting () {
      const that = this
      const map = that.mapChart.getModel().getComponent('bmap').getBMap()
      that.map = map
      map.setMinZoom(10)
    },
    initEvent () {
      const that = this
      const mapChart = that.mapChart
      const map = that.map
      mapChart.off('mouseover')
      mapChart.off('mouseout')
      // 二级厂鼠标悬浮 显示物流半径范围
      mapChart.on('mouseover', event => {
        if (event.seriesId === 'twoLevelPlant') {
          const [lng, lat] = event.data
          // 内部范围
          const circleInner = createCircleOverlay(lng, lat, that.logisticsRangeInnerRadius)
          // 外部范围
          const circleOuter = createCircleOverlay(lng, lat, that.logisticsRangeOuterRadius, 0.2)
          if (that.circleInner) {
            map.removeOverlay(that.circleInner)
          }
          if (that.circleOuter) {
            map.removeOverlay(that.circleOuter)
          }
          that.circleInner = circleInner
          that.circleOuter = circleOuter
          map.addOverlay(circleInner)
          map.addOverlay(circleOuter)
        }
      })
      mapChart.on('mouseout', () => {
        map.removeOverlay(that.circleInner)
        map.removeOverlay(that.circleOuter)
      })
    }
  }
}

</script>
<style lang="scss" scoped>
.com-map_contianer {
  box-sizing: border-box;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: #001f40;
  color: #fff;
  font-family: 'Microsoft YaHei';

  header {
    z-index: 3;
    position: relative;
    display: flex;
    justify-content: flex-start;
    background-image: url('~@/assets/images/header_bg_left.png');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    padding: 6px 15px 20px;

    .left {
      width: 324px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      h1 {
        margin: 0;
        font-weight: 700;
        font-size: 36px;
        text-shadow: 2px 1px 1px #00bcd4;
      }
    }

    .fa {
      line-height: 68px;
    }
  }

  .map {
    z-index: 1;
    position: fixed !important;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }
}
</style>
